

.content {
    height: calc(100vh - 66px);
    display:  flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 40px;
}

.content section {
    width: 650px;
}

.content h1 {
    font-size: 80px;
    text-align: center;
    margin-bottom: 10px;
}

.content h2 {
    font-size: 32px;
    margin-bottom: 10px;
}

.content b {
    color: aqua;
}

.content article {

    background-color: rgba(0, 0, 0, 0.548);
    border-radius: 15px;
    padding: 20px 30px;
    text-align: justify;
    font-size: 18px;

}

.actions {
    background-color: rgba(0, 0, 0, 0.548);
    border-radius: 15px;
    margin-top: 30px;
    padding: 20px;
    display: flex;
    justify-content: center;
    gap: 25px;

}

.quickNavigation{
    background-color: rgba(0, 0, 0, 0.548);
    border-radius: 15px;
    padding: 20px;
    max-width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px
}

.actions button, .quickNavigation button {
    padding: 10px 15px;
    border-radius: 12px;
    border: 0;
    font-weight: 700;
    font-size: 16px;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    transition: 0.3s;
    
}

.actions button {
    padding: 10px 20px;
}
.icon  {

    font-size: 18px;
    font-weight: 400;
}
.icon path {
    color: black;

}
.actions button .icon {
    margin-right: 5px;

}
.actions button:hover {
    background-color: aqua;
    padding-left: 25px;
    padding-right: 25px;
}

.quickNavigation button {

    width: 235px;
    
}

.quickNavigation button:hover {

    background-color: aqua;
    width: 245px;

}






