.slider {
    width: 65%;
}

.dotsbar  {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    height: 25px;
    width: 110px;
    background-color: rgba(0, 0, 0, 0.705);
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-radius: 15px;
}

.dot {
    height: 12px;
    width: 12px;
    background-color: #a7a7a7;
    border-radius: 100%;
}
.dot.active {
    background-color: rgb(124, 34, 207);
}

.angleLeft , .angleRight {

    position: absolute;
    top:50%;
    transform: translateY(-50%);
    font-size: 36px;
    background-color: rgba(0, 0, 0, 0.705);
    padding: 5px;
    border-radius: 15px;
}

.angleLeft {
    left: 20px;
}

.angleRight {

    right:20px;

}
